import topBar from  "../../components/topBar.vue"
import banner from  "../../components/banner.vue"

export default {
  components: {
    topBar,
    banner
  },
  data() {
    return {
      scrollTop: 0,                   // 距滚动距离
      isHeadShow: true,               // 头部作者名
      title: '',
      article_id: '',                 // 文章ID
      isAuthor: false,                // 是否是此文章的作者
      indicatorDots: true,
      autoplay: true,
      interval: 2000,
      duration: 500,
      indicatorActiveColor: '#ffffff',
      detail: {},
      userInfo: {},
      swiperList: [],               // 轮播图
      strings: [],                  // 评论信息
      speakVal: '',                 // 我的评论value值
      commentVal: '',               // 评论回复的value值
      comment_id: '',               // 是否评论评论、默认评论文章
      sumList: {
        zanTotal: 2000,
        starTotal: 1578,
        megTotal: 1959
      }
    }
  },
  // 接受首页传递的参数
  onLoad(option) {
    this.article_id = option.article_id
    this.getDetail(this.article_id)
  },
  computed: {
    statusBarHeight() {
      return this.$store.state.statusBarHeight
    }
  },
  methods: {
    goBack() {
      uni.navigateBack({
        delta: 1
        })
    },
    onScroll(e) {
      let that = this
      if(e.detail.scrollTop > that.scrollTop && that.isHeadShow) {
        that.isHeadShow= false
      } else if(e.detail.scrollTop < that.scrollTop && !that.isHeadShow){
        that.isHeadShow= true
      }
      that.scrollTop= e.detail.scrollTop
    },
    
    // 获取文章详情
    getDetail(id) {
      let that= this
      let params = {
        url: that.$api.articleDetail,
        data: {
        article_id: id
        }
      }
      that.$httpRequest(params).then((res) => {
        that.detail = res.data.detail
        // 判断是否是作者本人
        uni.getStorage({
          key: 'user_id',
          success: function(result) {
            if(result.data === res.data.detail.user_id) {
              that.isAuthor= true
            }
          }
        })
        
        var richtext =  res.data.detail.article_content
        const regex = new RegExp('img')
        richtext= richtext.replace(regex, `img style="max-width: 100%;"`)
        that.strings = richtext
      })
    },
    // 评论点赞
    zanAction(item, index) {
      let that= this
      let url = this.$api.commentUnlike
      if (item.islike === 'no') {
        url = this.$api.commentLike
      }
      let params= {
        url: url,
        data: {
          comment_id: item.id
        }
      }
      that.$httpRequest(params).then((res) => {
        switch(this.detail.comments_show[index].islike) {
          case 'yes':
            this.detail.comments_show[index].islike = 'no'
            this.detail.comments_show[index].likenum -= 1
            uni.showToast({
            	title: '取消点赞成功',
              icon: 'none'
            })
            break
          case 'no':
            this.detail.comments_show[index].islike = 'yes'
            this.detail.comments_show[index].likenum += 1
            uni.showToast({
            	title: '点赞成功',
              icon: 'none'
            })
            break
        }
      })
    },
    
    // 关注用户
    focusAuthor(article_id, status) {
      let that= this
      let url = this.$api.articleunFocus
      if (status === 'no') {
        url = this.$api.articleFocus
      }
      let params= {
        url: url,
        data: {
          article_id: article_id
        }
      }
      that.$httpRequest(params).then((res) => {
        switch(that.detail.isFocus) {
          case 'yes':
            that.detail.isFocus = 'no'
            break
          case 'no':
            that.detail.isFocus = 'yes'
            break
        }
      })
    },
    
    // 评论回复点赞
    commentZanAction(li, index, num) {
      let that= this
      let url = this.$api.commentreplyunlike
      if (li.isreplylike === 'no') {
        url = this.$api.commentReplyLike
      }
      let params= {
        url: url,
        data: {
          reply_id: li.id
        }
      }
      that.$httpRequest(params).then((res) => {
        switch(this.detail.comments_show[index].replys[num].isreplylike) {
          case 'yes':
            this.detail.comments_show[index].replys[num].isreplylike = 'no'
            this.detail.comments_show[index].replys[num].replylikenum -= 1
            uni.showToast({
            	title: '取消点赞成功',
              icon: 'none'
            })
            break
          case 'no':
            this.detail.comments_show[index].replys[num].isreplylike = 'yes'
            this.detail.comments_show[index].replys[num].replylikenum += 1
            uni.showToast({
            	title: '点赞成功',
              icon: 'none'
            })
            break
        }
      })
    },
    
    // 文章点赞
    likeArticle() {
      let that= this
      let url = this.$api.articleLike
      if (this.detail.isLike) {
        url = this.$api.articleunLike
      }
      let params= {
        url: url,
        data: {
          article_id: this.article_id
        }
      }
      that.$httpRequest(params).then((res) => {
        let isLike= that.detail.isLike,
          count= that.detail.articlelike_count
        that.detail.articlelike_count= isLike? count - 1: count + 1
        that.detail.isLike= !isLike
      })
    },
    
    // 分享按钮
    onShareAppMessage(res) {
      let that= this,
        params = {
          url: this.$api.articleShare,
          method: 'POST',
          data: {
            article_id: that.article_id,
            source: 'wechat'
          }
        }
      if (res.from === 'button') {// 来自页面内分享按钮
        that.$httpRequest(params).then((res) => {
          uni.showToast({
            title: '分享成功',
            icon: 'none'
          })
        })
      }
      return {
        title: that.detail.article_title,
        path: '/pages/index/article/detail?article_id=' + that.article_id
      }
    },
    
    // 删除文章
    deleteArticle(article_id) {
      let that= this,
        params = {
          url: this.$api.articleDelete,
          method: 'POST',
          data: {
            article_id: that.article_id
          }
        }
      uni.showModal({
        title: '温馨提示',
        content: '确认删除此篇文章？',
        success: function (res) {
          if (res.confirm) {
            console.log('用户点击确定')
            that.$httpRequest(params).then((res) => {
              uni.showToast({
                title: '删除成功',
                icon: 'none',
                success: () => {
                  uni.navigateBack({
                    delta: 1
                  })
                }
              })
            })
          } else if (res.cancel) {
            uni.showToast({
              title: '已取消',
              icon: 'none'
            })
          }
        }
      })
    },
    
    // 发布评论
    addComment(e) {
      console.log(this.speakVal)
      let that= this,
        params= {
        url: this.$api.articleAddComment,
        data: {
          article_id: that.article_id,
          comment: e.detail.value
        },
      }
      that.$httpRequest(params).then((res) => {
        that.closeComment()
        that.getDetail(this.article_id)
        uni.showToast({
          title: '发布成功',
          icon: 'none'
        })
      })
    },
    // 评论回复
    addCommentReply(e) {
      let that= this,
        params= {
          url: this.$api.commentreply,
          data: {
            comment_id: this.comment_id,
            comment: e.detail.value
          }
        }
      that.$httpRequest(params).then((res) => {
        that.closeComment()
        that.getDetail(this.article_id)
        uni.showToast({
          title: '发布成功',
          icon: 'none'
        })
      })
    },
    // 评论评论
    commentComment(id) {
      this.comment_id= id
    },
    // 取消评论回复
    closeComment() {
      this.comment_id= ''
      this.commentVal= ''
      this.speakVal= ''
    }
  }
}
